@charset "UTF-8";
:root {
  --font-family: "Inter", sans-serif;
  --font-secondary: "InterTight", sans-serif;
  --content-width: 1300px;
  --container-offset: 1.25rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --fs-base: .875rem;
  --fs-lg: 1rem;
  --fs-md: .75rem;
  --fs-sm: .625rem;
  --fw-base: 400;
  --lh-base: normal;
  --title-color: #000;
  --text-color: #000;
  --primary-color: #000;
  --secondary-color: #F1F1F1;
  --secondary-color-100: #F9F8F8;
  --magenta-color: #7D4ACF;
  --magenta-color-100: #F7F3FE;
  --magenta-color-150: #F3ECFF;
  --magenta-color-200: #EDE5FC;
  --magenta-color-225: #ECE1FE;
  --magenta-color-250: #DECFFA;
  --magenta-color-400: #C09AFF;
  --magenta-color-450: #9663EB;
  --magenta-color-500: #5E31A8;
  --cold-color: #1E43C4;
  --cold-color-100: #EDF2FC;
  --cold-color-200: #DBE5F9;
  --cold-color-225: #DBE6FC;
  --cold-color-300: #E5EEFF;
  --cold-color-500: #19369D;
  --error-color: #DD2F2F;
  --error-color-100: #FAF0F0;
  --error-color-200: #F3E0E0;
  --error-color-225: #FAE3E3;
  --error-color-300: #FFF2F2;
  --error-color-500: #FB4E4E;
  --success-color: #1A8B4F;
  --success-color-100: #EDF6F1;
  --success-color-200: #DAECE3;
  --success-color-225: #D1EEDC;
  --success-color-250: #E1F6E8;
  --success-color-300: #8AF0A1;
  --success-color-400: #32BD60;
  --light-color: #fff;
  --dark-color: #000;
  --dark-color-300: #545454;
  --dark-color-350: #202223;
  --dark-color-400: #292929;
  --dark-color-475: #1C1C1C;
  --dark-color-500: #0F0F0F;
  --dark-color-550: #050505;
  --gray-color: #8E8E96;
  --gray-color-100: #F1F1F1;
  --gray-color-125: #F2F2F2;
  --gray-color-150: #fafafa;
  --gray-color-200: #F4F5F7;
  --gray-color-250: #f5f5f5;
  --gray-color-300: #E9EBF0;
  --gray-color-400: #E6E6E6;
  --gray-color-425: #D0D5DD;
  --gray-color-450: #C7C9CD;
  --gray-color-475: #A5A5A5;
  --gray-color-500: #909090;
  --body-bg: #fff;
  --rounded: 1rem;
  --shadow: 0px 0px .625rem .125rem rgba(0,0,0,0.375);
  --shadow-sm: 0px 0px .625rem .125rem rgba(0,0,0,0.25);
  --transition-duration: 300ms;
  --transition-timing-function: ease-in-out;
}

@media (min-width: 991.98px) {
  :root {
    --fs-base: 1rem;
    --fs-lg: 1.125rem;
    --fs-md: .875rem;
    --fs-sm: .75rem;
  }
}
