*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page-body {
  min-width: 20rem;
  min-height: 100%;
  margin: 0;
  font-family: var(--font-family, sans-serif);
  font-size: var(--fs-base);
  font-weight: var(--fs-base);
  line-height: var(--lh-base);
  color: var(--text-color);
  background-color: var(--body-bg);
}

.page-body,
.site-container,
.main {
  display: flex;
  flex-direction: column;
}

.site-container,
.main,
.main-section {
  flex: 1;
}

/*   */
::-webkit-scrollbar {
  width: 0.25rem; /*   */
  height: 0.25rem; /*     */
  background-color: var(--gray-color-250); /*   */
  border-radius: 0.75rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--gray-color-450); /*   */
  border-radius: 0.75rem;
  transition: background-color 0.2s ease;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--gray-color-500); /*     */
}

::-webkit-scrollbar-track {
  background-color: var(--gray-color-250); /*    */
  border-radius: 0.75rem;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important;
}

.is-invisible {
  opacity: 0 !important;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.text-underline {
  text-decoration-line: underline !important;
  text-decoration-thickness: 0.0625em;
  text-underline-offset: 0.125em;
}

.text-magenta-450 {
  color: var(--magenta-color-450) !important;
}

.text-gray {
  color: var(--gray-color) !important;
}

.stretched-link {
  border-radius: inherit;
  position: absolute;
  inset: 0;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.centered {
  text-align: center;
}

.image-success {
  filter: drop-shadow(0px 0px 0.75rem var(--success-color-300));
}

.image-error {
  filter: drop-shadow(0px 0px 0.75rem var(--cold-color));
}

.image-question {
  filter: drop-shadow(0px 0px 0.75rem var(--magenta-color-400));
}

.image-success-sm {
  filter: drop-shadow(0px 0px 0.25rem var(--success-color-300));
}

.image-error-sm {
  filter: drop-shadow(0px 0px 0.25rem var(--cold-color));
}

.image-question-sm {
  filter: drop-shadow(0px 0px 0.25rem var(--magenta-color-400));
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-secondary);
  font-weight: 600;
  line-height: 1;
  color: var(--title-color);
}

