}

.text-xl {
  font-size: 1.25rem;
}

.text-lg {
  font-size: 1.125rem;
}

.text-base {
  font-size: var(--fs-base);
}

.text-md {
  font-size: 0.875rem;
}

.text-sm {
  font-size: 0.75rem;
}

.family-default {
  font-family: var(--font-family);
}

.family-secondary {
  font-family: var(--font-secondary);
}

.font-regular {
  font-weight: normal;
}

.font-medium {
  font-weight: medium;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: bold;
}

.font-style-normal {
  font-style: normal;
}

.font-style-italic {
  font-style: italic;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-padding {
  width: 100%;
  padding-inline: max(var(--container-offset), (100% - var(--content-width)) / 2);
}

.section {
  margin-bottom: 6rem;
}
.section:last-child {
  margin-bottom: 7.5rem;
}
.section-supheader {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem;
}
@media (min-width: 991.98px) {
  .section-supheader {
    margin-bottom: 1rem;
  }
}
.section-header {
  margin-bottom: 1.25rem;
