  font-weight: 500;
  line-height: 1.4;
  color: var(--gray-color);
}
.section.section-widget {
  margin-block: 2.5rem;
}

.icon {
  --_size: 1rem;
  --_color: currentColor;
  width: var(--_size);
  height: var(--_size);
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--_color);
}
.icon svg {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
  object-position: center;
}

.icon-container {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.icon-container > .icon,
.icon-container > .image {
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: opacity, visibility;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}
.icon-container > .icon:last-child,
.icon-container > .image:last-child {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  inset: 0;
}

.btn {
  --_width: initial;
  --_height: initial;
  --_fs: var(--fs-base);
  --_fw: 500;
  --_lh: 1;
  --_ls: normal;
  --_color: var(--text-color);
  --_bg: transparent;
  --_border-width: 0;
  --_border-style: solid;
  --_border-color: transparent;
  --_rounded: 0;
  --_padding: 0;
  --_transform: translateZ(0);
  --_transition-duration: var(--transition-duration);
  --_transition-timing-function: var(--transition-timing-function);
  --_color-hover: var(--magenta-color);
  --_bg-hover: transparent;
  --_border-color-hover: transparent;
  --_transform-hover: translateZ(0);
  --_color-active: var(--magenta-color);
  --_bg-active: transparent;
  --_border-color-active: transparent;
  --_transform-active: initial;
  --_color-disabled: var(--magenta-color);
  --_bg-disabled: transparent;
  --_border-color-disabled: transparent;
  --_transform-disabled: initial;
  --_icon-size: 1rem;
  --_icon-color: currentColor;
  width: var(--_width);
  height: var(--_height);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: var(--_fs);
  font-weight: var(--_fw);
  line-height: var(--_lh);
  letter-spacing: var(--_ls);
  color: var(--_color);
  cursor: pointer;
  background-color: var(--_bg);
  border: var(--_border-width) var(--_border-style) var(--_border-color);
  border-radius: var(--_rounded);
  padding: var(--_padding);
  outline: none;
  transform: var(--_transform);
  transition-property: color, background, border, transform, opacity, visibility;
  transition-duration: var(--_transition-duration);
  transition-timing-function: var(--_transition-timing-function);
}
@media (hover: hover) {
  .btn:is(:hover, :focus-visible) {
    color: var(--_color-hover);
    background-color: var(--_bg-hover);
    border-color: var(--_border-color-hover);
    transform: var(--_transform-hover);
  }
}
.btn:is(:active, .is-active, [aria-expanded=true]) {
  color: var(--_color-active);
  background-color: var(--_bg-active);
  border-color: var(--_border-color-active);
  transform: var(--_transform-active);
}
.btn:disabled, .btn[disabled], .btn[inert] {
  color: var(--_color-disabled);
  background-color: var(--_bg-disabled);
  border-color: var(--_border-color-disabled);
  transform: var(--_transform-disabled);
  user-select: none;
  pointer-events: none;
}
.btn .icon {
  --_size: var(--_icon-size);
  --_color: var(--_icon-color);
}

.label-check {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  user-select: none;
}
.label-check > * {
  pointer-events: none;
}

.btn-check {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
.btn-check + .btn {
  --_padding: .25rem;
}
.btn-check:checked + .btn {
  --_color: var(--primary);
  --_color-hover: var(--primary);
  --_color-active: var(--primary);
}
.btn-check:checked + .btn .icon-container > .icon:first-child,
.btn-check:checked + .btn .icon-container > .image:first-child {
  opacity: 0;
  visibility: hidden;
}
.btn-check:checked + .btn .icon-container > .icon:last-child,
.btn-check:checked + .btn .icon-container > .image:last-child {
  opacity: 1;
  visibility: visible;
}

.label-toggle {
  --_width: 2.5rem;
  --_height: 1.5rem;
  --_size-toggle: 1.25rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}
@media (min-width: 991.98px) {
  .label-toggle {
    --_width: 2.75rem;
    --_height: 1.75rem;
    --_size-toggle: 1.5rem;
    gap: 0.75rem;
  }
}
.label-toggle > * {
  pointer-events: none;
  user-select: none;
}
.label-toggle-container {
  flex-shrink: 0;
  position: relative;
}
.label-toggle-slider {
  display: block;
  width: var(--_width);
  height: var(--_height);
  background-color: var(--gray-color-450);
  border-radius: 1.5rem;
  transition-property: background;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}
.label-toggle-slider::before {
  content: "";
  display: block;
  width: var(--_size-toggle);
  height: var(--_size-toggle);
  background-color: var(--light-color);
  box-shadow: 0 3px 1px 0 rgba(0, 0, 0, 0.06), 0 3px 7px 0 rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0.125rem;
  transform: translateY(-50%);
  transition-property: left;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}
.label-toggle-content {
  font-weight: 500;
  line-height: 1.4;
}

.btn-toggle {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
.btn-toggle:checked + .label-toggle-slider {
  background-color: var(--magenta-color);
}
.btn-toggle:checked + .label-toggle-slider::before {
  left: calc(100% - var(--_size-toggle) - 0.125rem);
}

.btn-group {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  background-color: var(--gray-color-100);
  border-radius: 2.5rem;
  padding: 0.25rem;
}
.btn-group .btn {
  --_fs: .875rem;
  --_padding: .5rem 1.25rem;
  --_color: var(--gray-color);
  --_rounded: 2.25rem;
  --_color-hover: var(--primary-color);
  --_bg-hover: var(--light-color);
  --_color-active: var(--primary-color);
  --_bg-active: var(--light-color);
  min-width: 10rem;
}

.btn-group--plaintext {
  gap: 2rem;
  background-color: transparent;
  border-radius: 0;
  padding: 0;
}
.btn-group--plaintext .btn {
  --_padding: .25rem 0;
  --_bg-hover: transparent;
  --_bg-active: transparent;
  min-width: initial;
}

.btn-menu {
  --_padding: .375rem;
  --_icon-size: 1.25rem;
  margin: calc(var(--_padding) * -1);
}

.btn-close {
  --_icon-size: 1.5rem;
}

.btn-back {
  --_color: var(--magenta-color);
  --_color-hover: var(--magenta-color-500);
  --_color-active: var(--magenta-color-500);
  --_icon-size: 1.5rem;
}

.btn-clear {
  --_color: var(--light-color);
  --_bg: var(--magenta-color);
  --_rounded: 50%;
  --_color-hover: var(--light-color);
  --_bg-hover: var(--magenta-color-500);
  --_color-active: var(--light-color);
  --_bg-active: var(--magenta-color-500);
}
.btn-clear.btn-sm {
  --_width: 1.25rem;
  --_height: 1.25rem;
  --_padding: 0;
  --_icon-size: 1.125rem;
  flex-shrink: 0;
}
.btn-clear.btn-md {
  --_width: 2.5rem;
  --_height: 2.5rem;
  --_bg: var(--magenta-color-200);
  --_padding: 0;
  --_icon-size: 1.125rem;
  --_bg-hover: var(--magenta-color-200);
  --_bg-active: var(--magenta-color-200);
  flex-shrink: 0;
  box-shadow: 0px 2px 4px 0px rgba(17, 12, 8, 0.02);
}
.btn-clear.btn-md .icon {
  background-color: var(--magenta-color);
  border-radius: 50%;
  transition-property: background;
  transition-duration: var(--_transition-duration);
  transition-timing-function: var(--_transition-timing-function);
}
@media (hover: hover) {
  .btn-clear.btn-md:is(:hover, :focus-visible) .icon {
    background-color: var(--magenta-color-500);
  }
}
.btn-clear.btn-md:is(:active, .is-active) .icon {
  background-color: var(--magenta-color-500);
}

.btn-resume {
  --_color: var(--primary-color);
  --_bg: var(--secondary-color-100);
  --_color-hover: var(--light-color);
  --_bg-hover: var(--primary-color);
  --_color-active: var(--light-color);
  --_bg-active: var(--primary-color);
  justify-content: space-between;
}
.btn-resume.btn-lg {
  --_padding: .75rem 1rem;
}
@media (min-width: 991.98px) {
  .btn-resume.btn-lg {
    --_padding: 1rem 1.5rem;
  }
}
.btn-resume .content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.btn-resume .content--user {
  text-align: end;
}
.btn-resume .content-toggle {
  font-size: var(--fs-md);
  color: var(--gray-color);
}
.btn-resume .content-toggle > * {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
}
.btn-resume .content-toggle > *:last-child {
  display: none;
}
.btn-resume .content-current {
  font-weight: 600;
  transition-property: color;
  transition-duration: var(--_transition-duration);
  transition-timing-function: ease-out;
}
.btn-resume .pictures {
  display: flex;
  align-items: center;
}
.btn-resume .pictures .image {
